.headerline {
  width: 0%;
  height: 3px;
  transition: width 0.5s;
  background: 
    linear-gradient(to right, #4da4dd,  #033a61) border-box;
}

.header:hover .headerline {
  width: 100%;
}



section{
  display: block;
  width: 80px;
  height: 5px;
  margin: 10px auto;
  background: 
    linear-gradient(to right, #4da4dd,  #033a61) border-box;
    
}

.reviewsText::-webkit-scrollbar {
  display:none;
}

.aboutText::-webkit-scrollbar {
  display:none;
}

html {
  
}

body::-webkit-scrollbar {
  display:none;
}

.icon:hover {
  animation: pulse 1s linear infinite;
}

@keyframes pulse {
  0%,100% { transform: scale(1); }
  50% { transform: scale(1.2);
  }
}

.arrow:hover {
  animation: MoveUpDown 1s linear infinite;
}

@keyframes MoveUpDown {
  0%, 100% {
    bottom: -5px;
  }
  50% {
    bottom: -15px;
  }
}

.circle {
  position: absolute;
  padding: 0;
  margin: 13px auto;
  width: 51px;
  height: 51px;
  border-radius: 50%;
  border:1px solid black ;
  list-style: none;
  overflow: hidden;
  left: 10px;
}
.inner {
  position: absolute;
  border: 1px solid black;
  padding: 0;
  margin: 1em auto;
  width: 187px;
  height: 205px;
  border-radius: 50%;
  list-style: none;
  overflow: hidden;
  left: -70px;
  top: -95px;
}
li {

}
li .color {
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 50%;
  transform-origin: 0% 100%;
}
li:first-child .color {
  transform: rotate(0deg) skewY(30deg);
}
li:nth-child(2) .color {
  transform: rotate(120deg) skewY(30deg);
}
li:nth-child(3) .color {
  transform: rotate(240deg) skewY(30deg);
}
li:first-child .color {
  background: #033a61;
}
li:first-child .color:hover {
  background: #0095ff;
}
li:nth-child(2) .color {
  background: rgb(158, 158, 9);
}
li:nth-child(2) .color:hover {
  background: rgb(255, 251, 0);
}
li:nth-child(3) .color {
  background: #00250b;
}
li:nth-child(3) .color:hover {
  background: rgb(0, 255, 76);
}
.link{
  
  color: rgb(158, 158, 9);
  border-bottom: 1px dotted #000;
  text-decoration: none;
  transition: color 200ms linear;
}

.link:hover{
  color: yellow;
  transition: color 200ms linear;
}
